/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  border-radius: 10px;
  background-color: #F5F5F5; }

/*定义滚动条轨道 内阴影+圆角*/
::-webkit-scrollbar-track {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #F5F5F5; }

/*定义滑块 内阴影+圆角*/
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  /* 线性渐变 */
  background-image: -webkit-gradient(linear, left bottom, right top, color-stop(0.44, #3cba92), color-stop(0.72, #fdbb2d), color-stop(0.86, #fdbb2d));
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out; }

/*定义滑块悬浮样式*/
::-webkit-scrollbar-thumb:hover {
  background-image: -webkit-gradient(linear, left bottom, left top, color-stop(0.44, #fdbb2d), olor-stop(0.72, #fdbb2d), color-stop(0.86, #3cba92));
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out; }

.canvas-container {
  border: 1px solid #f1f1f1;
  border-radius: 8px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  -ms-border-radius: 8px;
  -o-border-radius: 8px;
  overflow: hidden; }

.option-li {
  border-radius: 8px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  -ms-border-radius: 8px;
  -o-border-radius: 8px;
  overflow: hidden; }

.ul .li {
  border-radius: 8px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  -ms-border-radius: 8px;
  -o-border-radius: 8px;
